@tailwind base;
@tailwind components;
@tailwind utilities;

body, html {
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: medium;
}

section {
    max-width: 1920px;
    margin: 0 auto;
}
/* variables*/
:root {
    --header-height: 57px;
}

/* Header */
.header {
    @apply fixed top-0 w-full bg-white/80 backdrop-blur-sm z-50 border-b;
}

/* Navigation */
.navigation {
    @apply container mx-auto px-4 py-4;
}

.navigation__list {
    @apply flex justify-center gap-8;
}

.navigation__link {
    @apply text-gray-700 hover:text-orange-600;
}

/* Footer */
.footer {
    @apply bg-gray-100 py-8;
}

.footer__container {
    @apply container mx-auto px-4;
}

.footer__text {
    @apply text-center text-gray-600;
}

/* Hero Section */
.hero {
    @apply pb-16 pt-[var(--header-height)];
}

.hero__container {
    @apply flex flex-row bg-gradient-to-r from-primary-main to-white rounded-md;
    align-items: center;
    justify-content: center;
}

.hero__photo {
    max-width: 600px;
}

.hero__photo>img {
    @apply rounded-md;
}

.hero__text {
    @apply px-4 pt-4;
}

.hero__title {
    @apply text-4xl lg:text-5xl font-bold mb-6 ;
    font-style: italic;
}

.hero__description {
    @apply text-xl mb-8;
    font-size: 1.5rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
}

.hero__buttons {
    @apply flex gap-3;
}

.button-lg {
    @apply h-11 px-8 rounded-md font-medium transition-colors focus:outline-none focus:ring-2 focus:ring-orange-600 focus:ring-offset-2;
}

/* Card */
.card {
    @apply rounded-lg border shadow-sm;
}

.card__content {
    @apply p-6 pt-0;
}

/* Общие классы */
.container {
    @apply mx-auto px-4;

}

.text-bold {
    @apply font-bold;
}

/* Testimonials */
.testimonials {
    @apply py-16 bg-gray-50;
}

.testimonials__title {
    @apply text-3xl font-bold mb-12 text-center;
}

.testimonials__grid {
    @apply grid md:grid-cols-2 gap-8;
}

.testimonial-card__text {
    @apply mb-4 text-gray-600;
}

.testimonial-card__name {
    @apply font-semibold;
}

/* About Section */
.about {
    @apply py-16 bg-white;
}

.about__container {
    @apply container;
}

.about__title {
    @apply text-3xl font-bold mb-8 text-center;
}

.about__text {
    @apply text-lg mb-6;
}

.about__grid {
    @apply grid md:grid-cols-2 gap-8 mb-12;
}

.about__card-title {
    @apply text-xl font-bold mb-4;
}

.about__card-list {
    @apply space-y-4;
}

.about__card-item {
    @apply flex items-start gap-3;
}

.about__card-icon {
    @apply mt-1 text-orange-600;
}

.about__card-description {
    @apply text-gray-600;
}

/* Features Section */
.features {
    @apply py-16;
}

.features__container {
    @apply container;
}

.features__grid {
    @apply grid md:grid-cols-2 gap-8;
}

.features__card {
    @apply rounded-lg border shadow-sm;
}

.features__card-content {
    @apply pt-6;
}

.features__card-title {
    @apply text-xl font-bold mb-4;
}

.features__card-list {
    @apply space-y-4;
}

.features__card-item {
    @apply flex items-start gap-3;
}

.features__card-icon {
    @apply mt-1 text-orange-600;
}

.features__card-description {
    @apply text-gray-600;
}

/* CourseFeature */
.course-feature {
    @apply flex items-start gap-3;
}

.course-feature__icon {
    @apply mt-1 text-orange-600;
}

.course-feature__title {
    @apply font-semibold;
}

.course-feature__description {
    @apply text-gray-600;
}
